// require.ensure 是 Webpack 的特殊语法，用来设置 code-split point
const Home = resolve => {
  require.ensure(['./views/index.vue'], () => {
    resolve(require('./views/index.vue'))
  })
}

const List = resolve => {
  require.ensure(['./views/list.vue'], () => {
    resolve(require('./views/list.vue'))
  })
}

const routers = [{
  path: '/',
  name: 'index',
  component: Home
}, {
  path: '/list',
  name: 'list',
  component: List
}, {
  path: '/topic/:id',
  name: 'topic',
  component(resolve) {
    require.ensure(['./views/topic.vue'], () => {
      resolve(require('./views/topic.vue'))
    })
  }
}, {
  path: '/login',
  name: 'login',
  component(resolve) {
    require.ensure(['./views/login.vue'], () => {
      resolve(require('./views/login.vue'))
    })
  }
}, {
  path: '/near',
  name: 'near',
  component(resolve) {
    require.ensure(['./views/near.vue'], () => {
      resolve(require('./views/near.vue'))
    })
  }
}, {
  path: '/add',
  name: 'add',
  component(resolve) {
    require.ensure(['./views/new.vue'], () => {
      resolve(require('./views/new.vue'))
    })
  }
}, {
  path: '/follow',
  name: 'follow',
  component(resolve) {
    require.ensure(['./views/follow.vue'], () => {
      resolve(require('./views/follow.vue'))
    })
  }
}, {
  path: '/my',
  name: 'my',
  component(resolve) {
    require.ensure(['./views/my.vue'], () => {
      resolve(require('./views/my.vue'))
    })
  }
}, {
  path: '*',
  component: Home
}]

export default routers



